* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f5b6d3; /* Màu hồng nhạt */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  text-align: center;
}

.question h2 {
  font-size: 24px;
  color: #ff4081; /* Màu hồng đậm */
}

.btn-container {
  display: flex; /* Sử dụng flexbox để căn chỉnh các nút nằm ngang */
  justify-content: center;
  align-items: center;
  gap: 20px; /* Đảm bảo các nút có khoảng cách đều đặn */
  position: relative;
}

.btn {
  background-color: #ff4081;
  border: none;
  color: white;
  padding: 10px 20px;
  margin: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: transform 0.3s;
  border-radius: 5px;
}

.btn:hover {
  background-color: #f50057;
}

.yes {
  margin-top: 68px;
  margin-right: 120px;
}

.result {
  margin-top: 20px;
}

.heart {
  width:100%;
}

p {
  font-size: 24px;
  color: #ff4081;
  margin-top: 10px;
}

/* Không sử dụng animation, chỉ di chuyển nút No */
.no {
  position: absolute; /* Đảm bảo nút có thể di chuyển tự do trong container */
}
